@import "variables";
@import "grid";
@import "language";
@import "typography";
@import "header";
@import "btns";
@import "modal";
@import "animation/start";
@import "animation/perl";
@import "animation/lua";

*,
*:after,
*:before {
  box-sizing: border-box;
}

body {
  background-color: #D6E2FA;
  background-image: url('/img/bg.jpg');
  text-align: center;
  padding: 20px 32px;
  font-family: 'Montserrat', sans-serif;
  margin: 0;

  @media (max-width: 600px) {
    padding: 20px 12px;
  }
}

img {
  max-width: 100%;
}
